.loader {
  width: 24px;
  height: 24px;
  border: 4px solid #666565;
  border-bottom-color: #ffffff;
  border-radius: 50%;
  display: block;
  margin: 20px auto;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  }

  @keyframes rotation {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}
